import React, { Component } from "react";
import "./AnchorRegular.css";

class AnchorLinkRegular extends Component {
  render() {
    const { title, link } = this.props.data;

    return (
      <div className="AnchorLink">
        <a href={link}>{title}</a>
      </div>
    );
  }
}

export default AnchorLinkRegular;