import React, { Component } from 'react';
import "../DeptProfile/styles.css";
import HeadingTwo from '../../Components/Texts/HeadingTwo';

class DeptVision extends Component{
    
    DeptVision = {
        instanceID : "DeptProfile",
        title : "VISION"
    };
    DeptMission = {
        instanceID : "DeptProfile",
        title : "MISSION"
    }

    render() {
        const { vision, mission1, mission2, mission3 } = this.props.data;
        return (
             <React.Fragment>
                 <div className="profile">
                    <HeadingTwo data={this.DeptVision}/>
                    <p> {vision} </p>
                 </div>
                 <div className="profile">
                    <HeadingTwo data={this.DeptMission}/>
                    <ol>
                        <li>{mission1}</li>
                        <li>{mission2}</li>
                        <li>{mission3}</li>
                    </ol>
                 </div>
             </React.Fragment>
        );
    }
}
export default DeptVision;
